<template>
  <div
   id="d-calendar"
   :class="[
      `d-calendar-${type}`,
      `d-calendar-${size}`,
      {'d-calendar-shadow': shadow}
    ]"
  >
    <div class="calendar_header">
      <span class="calendar_title" title="">{{ dateTitle }}</span>
      <d-icon icon='d-icon-arrow-left' size='16' class="arrow-left page-arrow" @click="selectDate('prev')" title=""></d-icon>
      <d-icon icon='d-icon-arrow-right' size='16' class="arrow-right page-arrow" @click="selectDate('next')" title=""></d-icon>
      <d-button type="info" size="small" class="back-today" @click="selectDate('today')"></d-button>
      <span class="show_time">{{time}}</span>
    </div>
    <table class="calendar_table">
      <thead>
        <tr>
          <th v-for="(weekday, index) in showWeekDays" :key="index">{{ weekday }}</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(day,index) in days" :key="index">
          <td
            v-for="(value, key) in day"
            :key="key"
            @dblclick="pickDay(value)"
          >
          <div class="td-box" :class="{'disabled': value.disabled, 'picked': value.picked, 'remind-day': isShowValueMark(value)}">
            <span class="set_calendar" v-if="formatDate(value.year, value.month, value.showDate) === setCalendar.setCalendarDate && setCalendar.isSetCalendar && size!=='mini'">&#33;</span>
            <span class="set_calendar" v-else-if="formatDate(value.year, value.month, value.showDate) === setCalendar.setCalendarDate && setCalendar.isSetCalendar && size === 'mini'"></span>
            <div class="salarDate">{{value.showDate}}</div>
            <d-icon class="haveValue" v-if="isShowValueMark(value)"  icon='d-icon-light-outline' size='20'></d-icon>
            <div class="lunarDate" v-if="showLunar">
              {{value.festival ? value.festival : (value.lunarDate === '' ? value.lunarMonth + '' : value.lunarDate)}}
            </div>
          </div>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  name: "calender",
  components: {
    
  },
  data() {
    return {
      showCalendar: false,
      today: new Date(),
      selectedDate: "",
      dataDate: {},
      days: [],
      time: '',
      showSelecteDate: "",
      weekDays: ["", "", "", "", "", "", ""],
      pickDate: "",
      // 1900-2100
      lunarInfo: [0x04bd8, 0x04ae0, 0x0a570, 0x054d5, 0x0d260, 0x0d950, 0x16554, 0x056a0, 0x09ad0, 0x055d2, //1900-1909
        0x04ae0, 0x0a5b6, 0x0a4d0, 0x0d250, 0x1d255, 0x0b540, 0x0d6a0, 0x0ada2, 0x095b0, 0x14977, //1910-1919
        0x04970, 0x0a4b0, 0x0b4b5, 0x06a50, 0x06d40, 0x1ab54, 0x02b60, 0x09570, 0x052f2, 0x04970, //1920-1929
        0x06566, 0x0d4a0, 0x0ea50, 0x06e95, 0x05ad0, 0x02b60, 0x186e3, 0x092e0, 0x1c8d7, 0x0c950, //1930-1939
        0x0d4a0, 0x1d8a6, 0x0b550, 0x056a0, 0x1a5b4, 0x025d0, 0x092d0, 0x0d2b2, 0x0a950, 0x0b557, //1940-1949
        0x06ca0, 0x0b550, 0x15355, 0x04da0, 0x0a5b0, 0x14573, 0x052b0, 0x0a9a8, 0x0e950, 0x06aa0, //1950-1959
        0x0aea6, 0x0ab50, 0x04b60, 0x0aae4, 0x0a570, 0x05260, 0x0f263, 0x0d950, 0x05b57, 0x056a0, //1960-1969
        0x096d0, 0x04dd5, 0x04ad0, 0x0a4d0, 0x0d4d4, 0x0d250, 0x0d558, 0x0b540, 0x0b6a0, 0x195a6, //1970-1979
        0x095b0, 0x049b0, 0x0a974, 0x0a4b0, 0x0b27a, 0x06a50, 0x06d40, 0x0af46, 0x0ab60, 0x09570, //1980-1989
        0x04af5, 0x04970, 0x064b0, 0x074a3, 0x0ea50, 0x06b58, 0x055c0, 0x0ab60, 0x096d5, 0x092e0, //1990-1999
        0x0c960, 0x0d954, 0x0d4a0, 0x0da50, 0x07552, 0x056a0, 0x0abb7, 0x025d0, 0x092d0, 0x0cab5, //2000-2009
        0x0a950, 0x0b4a0, 0x0baa4, 0x0ad50, 0x055d9, 0x04ba0, 0x0a5b0, 0x15176, 0x052b0, 0x0a930, //2010-2019
        0x07954, 0x06aa0, 0x0ad50, 0x05b52, 0x04b60, 0x0a6e6, 0x0a4e0, 0x0d260, 0x0ea65, 0x0d530, //2020-2029
        0x05aa0, 0x076a3, 0x096d0, 0x04afb, 0x04ad0, 0x0a4d0, 0x1d0b6, 0x0d250, 0x0d520, 0x0dd45, //2030-2039
        0x0b5a0, 0x056d0, 0x055b2, 0x049b0, 0x0a577, 0x0a4b0, 0x0aa50, 0x1b255, 0x06d20, 0x0ada0, //2040-2049
        0x14b63, 0x09370, 0x049f8, 0x04970, 0x064b0, 0x168a6, 0x0ea50, 0x06b20, 0x1a6c4, 0x0aae0, //2050-2059
        0x0a2e0, 0x0d2e3, 0x0c960, 0x0d557, 0x0d4a0, 0x0da50, 0x05d55, 0x056a0, 0x0a6d0, 0x055d4, //2060-2069
        0x052d0, 0x0a9b8, 0x0a950, 0x0b4a0, 0x0b6a6, 0x0ad50, 0x055a0, 0x0aba4, 0x0a5b0, 0x052b0, //2070-2079
        0x0b273, 0x06930, 0x07337, 0x06aa0, 0x0ad50, 0x14b55, 0x04b60, 0x0a570, 0x054e4, 0x0d160, //2080-2089
        0x0e968, 0x0d520, 0x0daa0, 0x16aa6, 0x056d0, 0x04ae0, 0x0a9d4, 0x0a2d0, 0x0d150, 0x0f252, //2090-2099
        0x0d520 //2100
      ],
      // 
      sFtv: { "0101": "", "0214": "", "0305": "", "0308": "", "0312": "", "0315": "", "0401": "", "0501": "", "0504": "", "0512": "", "0601": "", "0701": "", "0801": "", "0910": "", "0928": "", "1001": "", "1006": "", "1024": "", "1224": "", "1225": "" },
      // 
      lFtv: { "0101": "", "0115": "", "0202": "", "0505": "", "0707": "", "0715": "", "0815": "", "0909": "", "1208": "", "1223": "", "1224": "", "0100": "" },
      // 
      solarTerm: ["", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", ""],
      // 24
      sTermInfo: [0, 21208, 43467, 63836, 85337, 107014, 128867, 150921, 173149, 195551, 218072, 240693, 263343, 285989, 308563, 331033, 353350, 375494, 397447, 419210, 440795, 462224, 483532, 504758],
      // 
      lunarMonthStr: ["", "", "", "", "", "", "", "", "", "", "", ""],
      // 
      lunarDayStrFirst: ['', '', '', ''],
      lunarDayStrLast: [ "", "", "", "", "", "", "", "", "",""],
      // 
      solarMonth: [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31],
    };
  },
  props: {
    value: [Date, Object, String],
    firstDayOfWeek: {
      type: Number,
      default: 1
    },
    shadow: {
      type: Boolean,
      default: false
    },
    showLunar: {
      type: Boolean,
      default: false
    },
    size: {
      type: String,
      default: 'normal',
      validator(val) {
        return ['normal', 'large', 'small', 'mini', 'full'].includes(val) || /^[0-9]+$/.test(val);
      }
    },
    type: {
      type: String,
      default: 'light',
      validator(val) {
        return ['dark', 'light'].includes(val);
      }
    },
    setCalendar: {
      type: Object,
      default() {
        return {
          setCalendarDate: '',
          isSetCalendar: false
        }
      }
    }
  },
  mounted() {
    setInterval(this.showTime, 100);
    let value = this.handleValueCalendar;
    if (!value) {
      this.getMonthData();
      return;
    }
    this.showSettingCalendar(value);
  },
  methods: {
    isShowValueMark(value) {
        let showTime;
      this.$store.state.remindList.map(element => {
        let time = new Date(Number(element.reminderSendTime*1000));
        let year = time.getFullYear();
        let mouth = time.getMonth()+1;
        let day = time.getDate();
        let resTime = `${year}-${mouth}-${day}`

        let calenderYear = value.year;
        let calenderMonth = value.month;
        let calenderDay = value.showDate;
        let calenderTime = `${calenderYear}-${calenderMonth}-${calenderDay}`
        
        if(resTime == calenderTime){
          showTime = true
          return;
        }
      })
      return showTime;
    },
    // 
    showSettingCalendar(value) {
      let year, month;
      year = value.year;
      month = value.month;
      this.getMonthData(year, month);
    },
    showTime() {
      let time = new Date(),
          hour = `0${time.getHours()}`.slice(-2),
          minute = `0${time.getMinutes()}`.slice(-2),
          second = `0${time.getSeconds()}`.slice(-2);
      this.time = `${hour}:${minute}:${second}`;
    },
    showPickDate(val, pickDate) {
      this.showSelecteDate = val;
      this.pickDate = pickDate;
    },
    // 
    format(date) {
      console.log('date', date);
      let year = date.getFullYear();
      let month = date.getMonth() + 1;
      let day = date.getDate();
      month = `0${month}`.slice(-2);
      day = `0${day}`.slice(-2);
      return `${year}-${month}-${day}`;
    },
    // 
    pickDay(value) {
      this.dataDate.days.forEach(value => {
        value.picked = false;
      });
      this.pickDate = value.showDate;
      if ((value.year < this.dataDate.year || value.month < this.dataDate.month && value.year <= this.dataDate.year)) {
        this.selectDate("prev");
      } else if (value.year > this.dataDate.year || (value.month > this.dataDate.month && value.year >= this.dataDate.year)) {
        this.selectDate("next");
      }
      value.picked = true;
      let date = new Date(value.year, value.month - 1, value.showDate);
      this.showSelecteDate = this.format(date);
      this.$emit("pickDay", date, this.dataDate.days);
    },
    changeDate(val) {
      this.selectedDate = val;
    },
    // 
    selectDate(type) {
      let currentDate = this.selectedDate ? this.selectedDate : this.today;
      let year = currentDate.getFullYear();
      let month = currentDate.getMonth() + 1;
      let date = currentDate.getDate();
      if (type === "prev") {
        month = month - 1;
        if (month <= 0) {
          year = year - 1;
          month = month + 12;
        }
      } else if (type === "next") {
        month = month + 1;
        if (month > 12) {
          year = year + 1;
          month = month - 12;
        }
      } else if (type === "today") {
        year = this.today.getFullYear();
        month = this.today.getMonth() + 1;
        this.pickDate = this.today.getDate();
        // this.showSelecteDate = this.format(this.today);
      }
      let day = new Date(year, month - 1, date);
      // day = this.format(day);
      this.changeDate(day);
      this.getMonthData(year, month);
    },
    // 
    getMonthData(year, month) {
      let ret = [];
      if (!year || !month) {
        year = this.today.getFullYear();
        month = this.today.getMonth() + 1;
      }
      // 
      let firstDay = new Date(year, month - 1, 1);
      let firstDayWeekDay = firstDay.getDay();
      if (firstDayWeekDay === 0) {
        firstDayWeekDay = 7;
      }
      year = firstDay.getFullYear();
      month = firstDay.getMonth() + 1;
      // 
      let lastDateOfLastMonth = this.prevMonthLastDay(year, month);
      // 
      let preMonthDayCount = this.prevMonthDays(firstDayWeekDay);
      // 
      let lastDate = this.currentMonthLastDate(year, month);
      // 4-66
      for (let i = 0; i < 7 * 6; i++) {
        // date
        let date = i + 1 - preMonthDayCount;
        // 
        let showDate = date;
        let thisMonth = month;
        let thisYear = year;
        let disabled = false;
        let picked = false;
        // 
        let pickDate = this.pickDate ? this.pickDate : this.today.getDate();
        if (showDate === pickDate) {
          picked = true;
        }
        if (date <= 0) {
          // 
          thisMonth = month - 1;
          showDate = lastDateOfLastMonth + date;
          disabled = true;
        } else if (date > lastDate) {
          // 
          thisMonth = month + 1;
          showDate = showDate - lastDate;
          disabled = true;
        }
        if (thisMonth === 0) {
          thisMonth = 12;
          thisYear = year - 1;
        }
        if (thisMonth === 13) {
          thisMonth = 1;
          thisYear = year + 1;
        }
        // 
        let lunar = this.lunarShowText(thisYear, thisMonth, showDate, lastDate);
        ret.push({
          year: thisYear,
          month: thisMonth,
          date: date,
          showDate: showDate,
          disabled: disabled,
          picked: picked,
          lunarDate: lunar.lunarRealDate,
          festival: lunar.festival,
          lunarMonth: lunar.lunarShowMonth,
          lunarYear: lunar.lunarYear,
          color: lunar.color,
          fontWeight: lunar.fontWeight
        });
      }
      this.dataDate = {
        year: year,
        month: month,
        days: ret
      };
      // 
      this.handleTableData(ret);
    },
    // table
    handleTableData(days) {
      this.days = [];
      let weekday = [];
      for (let i = 0; i < days.length; i++) {
        weekday.push(days[i]);
        if (i % 7 === 6) {
          this.days.push(weekday);
          weekday = [];
        }
      }
    },
    conssss(value,key) {
      // console.log('pick',value,key)
    },



    // Y
    getLunarYearDays(Y) {
      var sum = 348;
      var lunarMonth = new Array();
      var leapMonth = [0, 0];
      var flag = 0;
      var temp = 0x8000;
      for (let i = 0; i < 12; i++) {
        flag = this.lunarInfo[Y - 1900] & temp ? 1 : 0;
        lunarMonth[i] = flag;
        sum += flag;
        temp >>= 1;
      }
      leapMonth = this.getLunarMonth(Y);
      sum += leapMonth[1];
      return {
        sum: sum,
        leapMonth: leapMonth,
        lunarMonth: lunarMonth
      };
    },
    // ,
    getLunarMonth(Y) {
      if (this.lunarInfo[Y - 1900] & 0xf) {
        return [
          this.lunarInfo[Y - 1900] & 0xf,
          this.lunarInfo[Y - 1900] & 0x10000 ? 30 : 29
        ];
      }
      return [0, 0];
    },
    // 1900(1900-01-31)Y-M-D
    gregorianCalendar(Y, M, D) {
      var sum = 0;
      for (var i = 1900; i < Y; i += 1) {
        if (i % 400 == 0 || (i % 100 != 0 && i % 4 == 0)) sum += 366;
        else sum += 365;
      }
      for (var i = 0; i < M - 1; i += 1) sum += this.solarMonth[i];
      if (M > 2 && (Y % 400 == 0 || (Y % 100 != 0 && Y % 4 == 0))) sum += 1;
      sum += D - 1;
      return sum - 30;
    },
    // 1900Y
    lunarCalendar(Y, M, D) {
      var sum = 0;
      var temp = null;
      for (var i = 1900; i < Y; i += 1) {
        temp = this.getLunarYearDays(i);
        sum += temp.sum;
      }
      temp = this.getLunarYearDays(Y);
      for (var i = 0; i < M - 1; i++) sum += temp.lunarMonth[i] == 0 ? 29 : 30;
      if (temp.leapMonth[0] < M) sum += temp.leapMonth[1];
      sum += D - 1;
      return sum;
    },
    // 
    calendarConvert(Y, M, D) {
      var num = this.lunarCalendar(Y, M, D) - this.gregorianCalendar(Y, M, D);
      var demo = this.getLunarYearDays(Y);
      var result = 0;
      if (D > num) return [0, Y, M, D - num];
      M -= 1;
      if (M == 0) {
        M = 12;
        Y -= 1;
        demo = this.getLunarYearDays(Y);
      }
      if (M == demo.leapMonth[0]) result = 1;
      if (D == num)
        return [result, Y, M, demo.lunarMonth[M - 1] == 0 ? 29 : 30];
      if (num > D) num -= D;
      while (true) {
        var temp = 0;
        if (demo.leapMonth[0] == M && result == 1) {
          temp = demo.leapMonth[1];
        } else {
          temp = demo.lunarMonth[M - 1] == 0 ? 29 : 30;
        }
        if (temp > num) {
          num = temp - num;
          break;
        }
        num -= temp;
        if (num == 0) {
          if (demo.leapMonth[0] == M && result == 1) {
            num = demo.lunarMonth[M - 1] == 0 ? 29 : 30;
            result = 0;
          } else {
            M -= 1;
            if (M == 0) {
              M = 12;
              Y -= 1;
              demo = this.getLunarYearDays(Y);
            }
            if (demo.leapMonth[0] == M) {
              result = 1;
              num = demo.leapMonth[1];
            } else {
              num = demo.lunarMonth[M - 1] == 0 ? 29 : 30;
            }
          }
          break;
        }
        if (demo.leapMonth[0] == M && result == 1) result = 0;
        else {
          M -= 1;
          if (M == 0) {
            Y -= 1;
            M = 12;
            demo = this.getLunarYearDays(Y);
          }
          if (demo.leapMonth[0] == M) result = 1;
        }
      }
      return [result, Y, M, num];
    },
    // 
    sTerm(y, n) {
        var offDate = new Date((31556925974.7 * (y - 1900) + this.sTermInfo[n] * 60000) + Date.UTC(1900, 0, 6, 2, 5));
        return (offDate.getUTCDate());
    },
    // 
    lunarShowText(year, month, showDate, lastDate) {
      // 
      let lunar = this.calendarConvert(year, month, showDate),
          lunarDate = lunar[3],
          lunarMonth = lunar[2],
          lunarYear = lunar[1],
          festival,
          lunarRealDate,
          lunarShowMonth,
          color = "",
          fontWeight = '400';
      // 
      lunarRealDate = lunarDate < 11 ? this.lunarDayStrFirst[0] + this.lunarDayStrLast[lunarDate - 1] : (lunarDate < 20 ?  this.lunarDayStrFirst[1] + this.lunarDayStrLast[lunarDate - 11] : ((lunarDate === 20 ?  '' : (lunarDate < 30 ?  this.lunarDayStrFirst[2] + this.lunarDayStrLast[lunarDate - 21] : ''))));
      lunarShowMonth = this.lunarMonthStr[lunarMonth - 1];
      lunarYear = this.transYearToChinese(lunarYear);
      // 
      var tmp1 = this.sTerm(year, (month - 1) * 2) - 1;
      var tmp2 = this.sTerm(year, (month - 1) * 2 + 1) - 1;
      if (tmp1 === showDate - 1) {
        lunarRealDate = this.solarTerm[(month - 1) * 2];
        fontWeight = "700";
      }
      if (tmp2 === showDate - 1) {
        lunarRealDate = this.solarTerm[(month - 1) * 2 + 1];
        fontWeight = "700";
      }
      let solarFesDate = `0${month}`.slice(-2) + `0${showDate}`.slice(-2),
          lunarFesDate = `0${lunarMonth}`.slice(-2) + `0${lunarDate}`.slice(-2);
      for(let key in this.sFtv) {
        if(key === solarFesDate) {
          // lunarShowDate = this.sFtv[key].slice(0,3);
          festival = this.sFtv[key];
          color = "#f00";
          fontWeight = "700";
          break;
        }
      }
      for(let key in this.lFtv) {
        if(key === lunarFesDate) {
          // lunarShowDate = this.lFtv[key].slice(0,3);
          festival = this.lFtv[key];
          color = "#f00";
          fontWeight = "700";
          break;
        }
      }
      return {
        lunarRealDate,
        festival,
        lunarShowMonth,
        lunarYear,
        color,
        fontWeight
      }
    },
    transYearToChinese(lunarYear) {
      let transToChinese = ['', '', '', '', '', '', '', '', '', ''];
      lunarYear = lunarYear.toString().split('');
      for(let i = 0; i < lunarYear.length; i++) {
        lunarYear[i] = transToChinese[lunarYear[i]];
      }
      lunarYear = lunarYear.join('');
      return lunarYear;
    }
  },
  computed: {
    // 
    dateTitle() {
      let currentCalendar = this.selectedDate ? this.selectedDate : this.today;
      let year, month;
      year = currentCalendar.getFullYear();
      month = currentCalendar.getMonth() + 1;
      month = `0${month}`.slice(-2);
      return `${year}${month}`;
    },
    // 
    formatDate() {
      return function(year, month, date) {
        month = `0${month}`.slice(-2);
        date = `0${date}`.slice(-2);
        return `${year}-${month}-${date}`;
      }
    },
    // 
    handleValueCalendar() {
      if (!this.value) return;
      let year, month;
      if (this.value instanceof Date) {
        year = this.value.getFullYear();
        month = this.value.getMonth() + 1;
      } else if (this.value instanceof Object) {
        year = this.value.year;
        month = this.value.month;
      } else if(typeof(this.value) === 'string') {
        let value = this.value.split('-');
        year = value[0];
        month = value[1];
      }
      month = `0${month}`.slice(-2);
      let date = this.today.getDate();
      let day = new Date(year, month - 1, date);
      this.changeDate(day);
      return {
        year: year,
        month: month
      };
    },
    // 
    showWeekDays() {
      let weekDays = this.weekDays,
        firstDayOfWeek = this.firstDayOfWeek - 1;
      for (let i = 0; i < firstDayOfWeek; i++) {
        weekDays[i + 7] = weekDays[i];
      }
      return weekDays.slice(-7);
    },
    // 
    prevMonthDays() {
      return function(firstDayWeekDay) {
        let preMonthDayCount;
        if (firstDayWeekDay - this.firstDayOfWeek < 0) {
          preMonthDayCount = firstDayWeekDay - this.firstDayOfWeek + 1 - 1 + 7;
        } else {
          preMonthDayCount = firstDayWeekDay - this.firstDayOfWeek + 1 - 1;
        }
        return preMonthDayCount;
      };
    },
    // 
    prevMonthLastDay() {
      return function(year, month) {
        let lastDayOfLastMonth = new Date(year, month - 1, 0);
        let lastDateOfLastMonth = lastDayOfLastMonth.getDate();
        return lastDateOfLastMonth;
      };
    },
    // 
    currentMonthLastDate() {
      return function(year, month) {
        let lastDay = new Date(year, month, 0);
        let lastDate = lastDay.getDate();
        return lastDate;
      };
    }
  }
};
</script>

